import { Component, OnInit } from '@angular/core';
import { NavController } from "@ionic/angular";
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions/ngx';
import { Parse } from 'parse';
import { AlertController } from "@ionic/angular";

let parse = require("parse");

@Component({
  selector: 'app-address-resume',
  templateUrl: './address-resume.page.html',
  styleUrls: ['./address-resume.page.scss'],
})

export class AddressResumePage implements OnInit {
  
  address: any;
  
  constructor(
    public navigate: NavController, public nativePageTransitions: NativePageTransitions, public alert:AlertController) {
    Parse.initialize("C0XMchZu6Y9XWNUK4lM1UHnnuXhC3dcdpa5fGYpO", "EdN4Xnln11to6pfyNaQ5HD05laenoYu04txYAcfo");
    Parse.serverURL = 'https://parseapi.back4app.com/';
  }

  ngOnInit() {
    this.getAddress();
  }

  openPage() {
    let options: NativeTransitionOptions = {
      duration: 300, 
      iosdelay: 300
    }
    console.log(options);
    this.nativePageTransitions.fade(options);
    this.navigate.navigateRoot("/checkout");
  }

  goBack() {
    let options: NativeTransitionOptions = {
      duration: 300, 
      iosdelay: 300
    }
    console.log(options);
    this.nativePageTransitions.fade(options);
    this.navigate.navigateRoot("/shipping-address");
  }

  getAddress() {
    //end-point para añadir la direccion del usuario
    Parse.Cloud.run('getAddress', {
      userId: Parse.User.current().id,
    }).then((result) => {
      console.log(result);
     this.address = result;
    }, (error) =>{
    console.log(error);
  });
   }
}
